<script>
import Header from './common/Header.vue'
import SolarCalender from './common/SolarCalender.vue'
import ExaminationItem from './common/ExaminationItem.vue'
import {mapGetters , mapActions} from 'vuex'
import Footer from './common/Footer.vue'

export default {
  name:'Schedule',
  components:{
     'common-header': Header,
     'solar-calender': SolarCalender,
     'examination-item': ExaminationItem,
     'common-footer': Footer
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      titlename: '日程',
      year: 2017,
      month: 11,
      day:1,
      selectDays:[12,13],
      pageIndex: 1,
      pageSize:100
    }
  },
  created(){
    const curDate = new Date();
    this.year = curDate.getFullYear();
    this.month = curDate.getMonth() + 1;
    this.day = curDate.getDate();
    let date = this.year + '-' + this.month + '-' +this.day;
    this.getExamDate({month: this.month , year: this.year});
    this.clearExamRoomList();
    this.getExamRoomList({date , pageIndex: this.pageIndex ,pageSize: this.pageSize});
  },
  computed:{
    ...mapGetters(['examDate' , 'examRoomList'])
  },
  methods:{
    ...mapActions(['getExamDate' , 'getExamRoomList' , 'clearExamRoomList']),
    clickDayCallBack(date){
      this.day = date.day;
      date = date.year + '-' + date.month + '-' + date.day;
      this.clearExamRoomList();
      this.getExamRoomList({date ,pageIndex: this.pageIndex ,pageSize: this.pageSize});
    },
    changeDate(flag){
      if (flag === 'pre') {
        if (this.month === 1) {
          this.month = 12;
          this.year--;
        }
        else{
          this.month--;
        }
      }
      else{
        if (this.month === 12) {
          this.month = 1;
          this.year++;
        }
        else{
          this.month++;
        }
      }
      const date = this.year + '-' + this.month + '-' + this.day;
      this.clearExamRoomList();
      this.getExamRoomList({date ,pageIndex: this.pageIndex ,pageSize: this.pageSize});
      this.getExamDate({month: this.month , year: this.year});
    }
  }
}
</script>
<template>
    <div class="schedule">
      <common-header :title="titlename"></common-header> 
      <div class="schedule-condition">
        <div  class="choose-date">
          <img src="../assets/images/preDay.png" class="fl" @click = "changeDate('pre')">
          {{year}} 年 {{month}} 月 
          <img src="../assets/images/nextDay.png" class="fr" @click = "changeDate('next')">
        </div>
        <solar-calender :year="year" :month="month" :selectDays="examDate" :clickDayCallBack="clickDayCallBack"/>
      </div>
      <ul class="examination-list">
          <li class="examination-list-title">
            当日行程
            <span>共<i>{{examRoomList.length}}</i>场</span>
          </li>
          <li class="no-data" v-if="examRoomList.length === 0">未搜索到当日行程</li>
          <examination-item  v-for="(item , index) in examRoomList" style="margin-top:0;" :examinfo = "item" key = "index"/>
      </ul>
      <common-footer :currenttype ="2"/>
    </div>
</template>
<style lang="less" scoped>
 @import "../assets/css/schedule.less";
</style>


